<template>
  <el-aside class="index-aside" width="210px">
    <div class="index-aside-inner menulist">
      <div class="menulist-item">

        <el-menu :mode="2 == 1? 'horizontal':'vertical'" :unique-opened="true" class="el-menu-demo" default-active="">
          <el-menu-item index="0" @click="menuHandler('')"><i v-if="true" class="el-icon-menu el-icon-s-home"/>首页
          </el-menu-item>
          <el-submenu :index="'1'">
            <template slot="title">
              <i v-if="true" class="el-icon-menu el-icon-user-solid"/>
              <span>个人中心</span>
            </template>
            <el-menu-item :index="'1-1'" @click="menuHandler('updatePassword')">修改密码</el-menu-item>
            <el-menu-item :index="'1-2'" @click="menuHandler('center')">个人信息</el-menu-item>
          </el-submenu>
          <el-submenu v-for=" (menu,index) in menuList" :key="menu.id" :index="index+2+''"
                      style="border-color: rgb(50, 65, 87) !important">
            <template slot="title">
              <i v-if="true" class="el-icon-menu" :class="icons[index]"/>
              <span>{{ menu.mod_name }}</span>
            </template>
            <el-menu-item v-for=" (child,sort) in menu.child" :key="sort" :index="(index+2)+'-'+sort"
                          @click="menuHandler(child.mod_path)">{{ child.mod_name }}
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </div>
    </div>
  </el-aside>
</template>

<script>
import menu from '@/utils/menu'

export default {
  data() {
    return {
      menuList: [],
      dynamicMenuRoutes: [],
      role: '',
      icons: [
        'el-icon-s-cooperation',
        'el-icon-s-order',
        'el-icon-s-platform',
        'el-icon-s-fold',
        'el-icon-s-unfold',
        'el-icon-s-operation',
        'el-icon-s-promotion',
        'el-icon-s-release',
        'el-icon-s-ticket',
        'el-icon-s-management',
        'el-icon-s-open',
        'el-icon-s-shop',
        'el-icon-s-marketing',
        'el-icon-s-flag',
        'el-icon-s-comment',
        'el-icon-s-finance',
        'el-icon-s-claim',
        'el-icon-s-custom',
        'el-icon-s-opportunity',
        'el-icon-s-data',
        'el-icon-s-check',
        'el-icon-s-grid',
        'el-icon-menu',
        'el-icon-chat-dot-square',
        'el-icon-message',
        'el-icon-postcard',
        'el-icon-position',
        'el-icon-microphone',
        'el-icon-close-notification',
        'el-icon-bangzhu',
        'el-icon-time',
        'el-icon-odometer',
        'el-icon-crop',
        'el-icon-aim',
        'el-icon-switch-button',
        'el-icon-full-screen',
        'el-icon-copy-document',
        'el-icon-mic',
        'el-icon-stopwatch',
      ],
      menulistStyle: 'vertical',
      menulistBorderBottom: {},
    }
  },
  mounted() {
    let params = {
        role: this.$storage.get('role'),
      }
      this.$http({
        url: `${DOMAIN_API_SYS}/django7681v/menu_list/?role=${this.$storage.get('role')}`,
        method: "get",
        params: params
      }).then(({data}) => {
        this.menuList = data.data;
          this.$storage.set("menus", this.menuList);
      })
    this.role = this.$storage.get('role')
  },
  created() {
    setTimeout(() => {
      this.menulistStyleChange()
    }, 10)
    this.icons.sort(() => {
      return (0.5 - Math.random())
    })
    this.lineBorder()
  },
  methods: {
    lineBorder() {
      let style = 'vertical'
      let w = '1px'
      let s = 'solid'
      let c = '#ccc'
      if (style == 'vertical') {
        this.menulistBorderBottom = {
          borderBottomWidth: w,
          borderBottomStyle: s,
          borderBottomColor: c
        }
      } else {
        this.menulistBorderBottom = {
          borderRightWidth: w,
          borderRightStyle: s,
          borderRightColor: c
        }
      }
    },
    menuHandler(name) {
      let router = this.$router
      name = '/' + name
      router.push(name)
    },
    // 菜单
    setMenulistHoverColor() {
      let that = this
      return;
      this.$nextTick(() => {
        document.querySelectorAll('.menulist .el-menu-item').forEach(el => {
          el.addEventListener("mouseenter", e => {
            e.stopPropagation()
            el.style.backgroundColor = "rgba(101, 126, 253, 1)"
          })
          el.addEventListener("mouseleave", e => {
            e.stopPropagation()
            // el.style.backgroundColor = "rgb(50, 65, 87)"
            el.style.background = "none"
          })
          el.addEventListener("focus", e => {
            e.stopPropagation()
            el.style.backgroundColor = "rgba(101, 126, 253, 1)"
          })
        })
        document.querySelectorAll('.menulist .el-submenu__title').forEach(el => {
          el.addEventListener("mouseenter", e => {
            e.stopPropagation()
            el.style.backgroundColor = "rgba(101, 126, 253, 1)"
          })
          el.addEventListener("mouseleave", e => {
            e.stopPropagation()
            // el.style.backgroundColor = "rgb(50, 65, 87)"
            el.style.background = "none"
          })
        })
      })
    },
    setMenulistIconColor() {
      this.$nextTick(() => {
        document.querySelectorAll('.menulist .el-submenu__title .el-submenu__icon-arrow').forEach(el => {
          el.style.color = "rgb(50, 65, 87)"
        })
      })
    },
    menulistStyleChange() {
      this.setMenulistIconColor()
      this.setMenulistHoverColor()
      let str = "2"
      if (1 == str) {
        this.$nextTick(() => {
          document.querySelectorAll('.el-container .el-container').forEach(el => {
            el.style.display = "block"
            el.style.paddingTop = "60px" // header 高度
          })
          document.querySelectorAll('.el-aside').forEach(el => {
            el.style.width = "100%"
            el.style.height = "100%"
            el.style.paddingTop = '0'
          })
          document.querySelectorAll('.index-aside .index-aside-inner').forEach(el => {
            el.style.paddingTop = '0'
            el.style.width = "100%"
          })
        })
      }
      if (2 === str) {
        this.$nextTick(() => {
          document.querySelectorAll('.index-aside .index-aside-inner').forEach(el => {
            el.style.paddingTop = "60px"
          })
        })
      }
    },

  }
}
</script>
<style lang="scss" scoped>
.el-container {
  display: block;
}

.index-aside {
  position: relative;
  overflow: hidden;
  display: flex;
  flex-wrap: wrap;

  .menulistImg {
    font-size: 0;
    box-sizing: border-box;

    .el-image {
      margin: 0 auto;
      width: 100px;
      height: 100px;
      border-radius: 100%;
      display: block;
    }
  }

  .index-aside-inner {
    height: 100%;
    margin-right: -17px;
    margin-bottom: -17px;
    overflow: scroll;
    overflow-x: hidden !important;
    padding-top: 60px;
    box-sizing: border-box;

    &:focus {
      outline: none;
    }

    & /deep/ .el-menu {
      border: 0;
      background-color: transparent;
    }
  }

  .index-aside .index-aside-inner.menulist {
    height: 100% !important;
  }

  .menulist-item {
    width: 210px;
    margin: 0;
    border-radius: 0;
    border-width: 0 0 0px 0 !important;
    border-style: solid !important;

    background: rgb(52, 57, 87) !important;
    box-shadow: 0 0 6px rgba(30, 144, 255, .2);
    box-sizing: border-box;
  }

  .el-menu-demo {
    box-sizing: border-box;
    min-height: calc(100vh - 60px);

    & > .el-menu-item {
      width: auto;
      height: auto !important;
      line-height: 35px !important;
      padding: 10px 22px 10px 10px;
      color: rgba(255, 255, 255, 1);
      font-size: 14px;
      border-radius: 0;
      border-width: 0 0 1px 0;
      border-style: solid;
      border-color: rgba(218, 218, 218, 0.15) !important;
      background-color: rgb(50, 65, 87) !important;
      box-shadow: 0 0 6px rgba(255, 255, 255, 0);
      box-sizing: initial;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      box-sizing: border-box;

      .el-icon-menu {
        margin: 0 5px 0 0;
        padding: 0;
        width: 24px;
        line-height: 24px;
        color: rgba(144, 147, 153, 1);
        font-size: 16px;
        border-radius: 0;
        border-width: 0;
        border-style: solid;
        border-color: #fff;
        background-color: rgb(50, 65, 87) !important;
        box-shadow: 0 0 6px rgba(255, 255, 255, 0);
      }
    }

    .el-submenu {
    }

    & /deep/ .el-submenu__title {
      width: auto;
      height: auto !important;
      line-height: 35px !important;
      padding: 10px 22px 10px 10px;
      color: rgba(255, 255, 255, 1);
      font-size: 14px;
      border-radius: 0;
      border-width: 0 0 1px 0;
      border-style: solid;
      border-color: rgba(218, 218, 218, 0.15) !important;
      background-color: rgb(50, 65, 87) !important;
      box-shadow: 0 0 6px rgba(255, 255, 255, 0);
      box-sizing: initial;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      box-sizing: border-box;

      .el-icon-menu {
        margin: 0 5px 0 0;
        padding: 0;
        width: 24px;
        line-height: 24px;
        color: rgba(144, 147, 153, 1);
        font-size: 16px;
        border-radius: 0;
        border-width: 0;
        border-style: solid;

        background-color: rgb(50, 65, 87);
        box-shadow: 0 0 6px rgba(255, 255, 255, 0);
      }

      .el-submenu__icon-arrow {
        margin: 0 10px 0 0;
        padding: 0;
        width: 12px;
        line-height: 12px;
        color: rgba(144, 147, 153, 1) !important;
        font-size: 12px;
        border-radius: 0;
        border-width: 0;
        border-style: solid;

        background-color: rgb(50, 65, 87) !important;
        box-shadow: 0 0 6px rgba(255, 255, 255, 0);
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        text-align: center;
        display: block;
      }
    }

    & /deep/ .el-menu.el-menu--inline {
      //width: 150px;
      height: auto;
      padding: 0;
      margin: 0;
      //border-radius: 0px;
      //border-width: 0;
      //border-style: solid;
      //border-color: rgba(0, 0, 0, .3);
      //background-color: rgb(50, 65, 87);
      //box-shadow: 0 0 6px rgba(0, 0, 0, .3);

      .el-menu-item {
        width: 100%;
        height: 44px;
        line-height: 44px;
        padding: 0 0 0 30px !important;
        margin: 0;
        color: rgba(255, 255, 255, 1) !important;
        font-size: 14px;
        border-radius: 0;
        border-width: 0;
        border-style: solid;
        border-color: rgba(0, 0, 0, 0);
        background-color: #242f42 !important;
        box-shadow: 0 0 6px rgba(30, 144, 255, 0);
        text-align: left;
        min-width: auto;

        &.is-active {
          width: 100%;
          height: 44px;
          line-height: 44px;
          padding: 0 0 0 30px !important;
          margin: 0;
          color: rgba(255, 255, 255, 1) !important;
          font-size: 14px;
          border-radius: 0;
          border-width: 0;
          border-style: solid;
          border-color: rgba(0, 0, 0, 0);
          background-color: #409eff !important;
          box-shadow: 0 0 6px rgba(30, 144, 255, 0);
          text-align: left;
        }

        &:hover {
          width: 100%;
          height: 44px;
          line-height: 44px;
          padding: 0 0 0 30px !important;
          margin: 0;
          color: rgba(255, 255, 255, 1) !important;
          font-size: 14px;
          border-radius: 0;
          border-width: 0;
          border-style: solid;
          border-color: rgba(0, 0, 0, 0);
          background-color: #409eff !important;
          box-shadow: 0 0 6px rgba(30, 144, 255, 0);
          text-align: left;
        }
      }
    }
  }
}
</style>
